/* search s ================================================= */
.box.box-search {
  margin-bottom:40px;
  padding:$cntn_pd $cntn_pd ($cntn_pd - 5px) $cntn_pd !important;
  @media (max-width:$md_max_w_s) {
    padding:$cntn_pd_m $cntn_pd_m 0 $cntn_pd_m !important;
    margin-bottom:20px;
  }
  background:#e9e9e9;
  .form-group {
    > div[class*=col-] {
      padding-right:$cntn_pd_m;
      padding-left:$cntn_pd_m;
      margin-bottom:$cntn_pd_m - 5px;
      &:first-child {
        padding-right:0;
      }
      &:last-child {
        padding-right:0;
        padding-left:10px;
        @media (max-width:$md_max_w_m) {
          padding-left:0;
        }
      }
      &.input-group {}
      > div[class*=col-] {
        padding-right:0;
        padding-left:0;
      }
      @media (max-width:$md_max_w_m) {
        padding-right:0;
        padding-left:0;
      }
    }
    .input-group-btn {
      border-top:1px solid $border_clr;
      border-right:1px solid $border_clr;
      border-bottom:1px solid $border_clr;
      .btn {
        height:$btn_h - 2px;
      }
    }
    .btn {
      margin-left:0;
      margin-bottom:0;
      width:100%;
      &.input-group-addon {
        padding:0 10px;
        line-height:1 !important;
        // background:$btn_bg_clr1;
        background:$btn_fc;
      }
      &.btn-primary {
        width:100%;
      }
      &.btn-danger {
        width:50%;
      }
      .idrm-icon-search::before {
        content:url('#{$img_url}#{$i_search}');
      }
    }
    &.collapse {
      margin-top:$cntn_pd / 2;
      margin-bottom:0;
      .control-label {margin-right:$cntn_pd / 2;}
      .input-group-addon {padding:4px 12px;}
      .input-group.date {float:left;width:46%;}
      .input-group.date:last-child {float:right;}
      .input-group-gubun {float:left;padding:0;width:8%;line-height:$btn_h;text-align:center;}
      .btn {
        float:right;
        width:48%;
        &:first-child {float:left}
        &:only-child {width:100%;}
      }
      @media (max-width:$md_max_w_m) {
        margin-top:0;
        padding-bottom:$cntn_pd_m;
        /*
        .col-md-5,
        .col-md-3,
        .col-md-10,
        .col-md-2 {float:left;clear:both;}
        .col-md-3 {margin-top:10px}
        .col-md-2 {margin-top:10px;padding-top:20px;width:100%;border-top:1px solid $border_clr;}
        */
        .col-md-10 {padding-left:0;}
        .row {margin-right:0;margin-left:0;}
        .control-label {margin-top:10px;margin-bottom:5px;width:100%;}
        .input-group.date:first-child {clear:both;}
        .text-right {text-align:left;}
        div[class*="col-md-2"]:last-child {
          clear:both;
          margin-top:$cntn_pd_m * 2;
          padding-top:$cntn_pd_m;
          border-top:1px solid $border_clr;
        }
      }
    }
    &.collapse_02 {
      margin-top: 10px;
      margin-bottom: 0;
      .control-label {
        margin-right: 10px;
      }
      .input-group-addon {
        padding: 4px 12px;
      }
      .input-group.date {
        float: left;
        width: 46%;
        &:last-child {
          float: right;
        }
      }
      .input-group-gubun {
        float: left;
        padding: 0;
        width: 8%;
        line-height: 30px;
        text-align: center;
      }
      .btn {
        float: right;
        width: 48%;
        &:first-child {
          float: left;
        }
        &:only-child {
          width: 100%;
        }
      }
      @media (max-width:$md_max_w_m) {
        margin-top: 0;
        padding-bottom: 10px;
        .col-md-10 {
          padding-left: 0;
        }
        .row {
          margin-right: 0;
          margin-left: 0;
        }
        .control-label {
          margin-top: 10px;
          margin-bottom: 5px;
          width: 100%;
        }
        .input-group.date:first-child {
          clear: both;
        }
        .text-right {
          text-align: left;
        }
        div[class*="col-md-2"]:last-child {
          clear: both;
          margin-top: 20px;
          padding-top: 10px;
          border-top: 1px solid $border_clr;
        }
      }
    }
    .pa-top {padding-top:7px !important;}
    .pa-right {
      @media (min-width:$md_min_w_l) {
        padding-right:10px !important;
      }
    }
  }
  .input-group[class*="col-"] {
    float:left;
    padding-right:$cntn_pd;
    padding-left:$cntn_pd;
    @media (max-width:$md_max_w_s) {
      padding-right:0;
      padding-left:0;
    }
    .form-control {
    }
    .btn {
      margin:0;
      padding:0 10px;
      border:none;
      width:100%;
    }
  }
  .control-label{
    text-align:left;
  }
  .checkbox-inline,
  .radio-inline {
    margin-right:0;
    margin-top:9px;
    padding-right:20px;
    padding-left:0;
    &:last-child {
      padding-right:0;
    }
    > div[class*="icheckbox"] {
      margin-right:5px;
    }
  }
}
