/* form s =================================================== */
.input-group {
  &.input-search {
    display:table;
    border:1px solid $border_clr;
    .form-control {
      border:0;
      height:$btn_h - 2px;
    }
    .btn {
      width: 100%;
      height:$btn_h - 2px;
      margin-left:0;
      margin-bottom:0;
      padding:0;
      &.input-group-addon {
        padding:3px 10px 0 10px;
        min-width:auto;
        line-height:1 !important;
        border-color:transparent;
        .idrm-icon-search {
          &:before {
            content:url('#{$img_url}#{$i_search}');
          }
        }
      }
    }
  }
}
.input-group-addon {
  padding: 5px 12px !important;
}
.date-inline {
  div[class*="col-"] {
    padding:0;
    &:first-child {
      padding-right:7px;
    }
    &:last-child {
      padding-left:7px;
    }
    .date {
      width:100%;
    }
  }
  .date-gubun {
    float:left;
    width:2%;
    text-align:center;
    line-height:$btn_h;
  }
}

.check-btn-wrap,
.radio-btn-wrap {
  display:inline-block;
  margin:4px 0 0 0;
  padding:0;
  li {
    display:inline-block;
    margin-right:$cntn_pd;
    &:last-child {
      margin-right:0;
    }
    list-style:none;
    label {margin-left:5px;}
  }
  &.extra {
    margin-bottom:($cntn_pd / 2);
    .rd_title {

    }
  }
}

.short_input {
  display:inline-block !important;
  margin:0 $cntn_pd 0 ($cntn_pd - 10px);
  width:50px !important;
}

.short_input2 {
  display:inline-block !important;
  margin:0 $cntn_pd 0 ($cntn_pd - 10px);
  width:30px !important;
}

.web-editor-wrap {
  clear:both;
  padding:0 $cntn_pd $cntn_pd $cntn_pd;
  border:1px solid $border_clr;
  .web-editor-box {
    min-height:40px;
  }
}

.input-preview-box {
  input[type="file"] {
    position:absolute;
    width:1px;
    height:1px;
    padding:0;
    margin:-1px;
    overflow:hidden;
    clip:rect(0,0,0,0);
    border:0;
  }
  .upload-display {
    display:none;
    .upload-thumb-wrap {
      width:80px;
      height:80px;
      overflow:hidden;
      border:1px solid #ddd;
      img {
        width:100%;
        height:100%;
      }
    }
  }
  .txt-file-name-box {
    display:none;
    margin-left:10px;
    /*vertical-align:top;*/
    .txt-file-name {
      display:inline-block;
      max-width:200px;
      overflow:hidden;
      text-overflow:ellipsis;
      border:0;
    }
  }
  .btn-input-file-del {
    width:13px;
    height:13px;
    background:url('#{$img_url}icon_modal_close.png') no-repeat;
    background-size:cover;
    vertical-align:top;
    border:0;
  }
  &.file-inc {
    .btn-info {
      display:none;
    }
    .upload-display,
    .txt-file-name-box {
      display:inline-block;
    }
  }
}
