/* adminlte-edit s ========================================== */
.login-page {
  background:$lnb_bg_clr1;
}
.login-box {
  padding-top:5%;
  margin-bottom:33px;
  width:90%;
  max-width:550px;
  border:2px;
  border-style:solid;
  border-image-slice:1 !important;
  border-image-width:20px;
  border-image: linear-gradient(to bottom, #e16a24, #c11111);
  background:#fff;
  box-shadow: 2.6px 4.2px 10px rgba(223,90,27,0.28);
}
.login-logo,
.register-logo {
  margin-bottom:0;
  img {
    width:90%;
    max-width:300px;
  }
  b {
    padding:0 5%;
    display:block;
    font-size:3.3rem;
    color:#461518;
    overflow:hidden;
  }
}
.login-box-body,
.register-box-body {
  padding:11% 12% 14% 12%;
  .form-group {
    margin-bottom:15px;
  }
  .form-control {
    padding-left:40px;
    height:56px;
    border:0;
    background:#f0f0f0;
    &::placeholder {
      color:#2a2a2a;
    }
  }
  .form-control-feedback {
    top:13px;
    right:unset;
    left:4px;
  }
  .glyphicon-pencil:before {
    content:'';
    display:inline-block;
    width:14px;
    height:14px;
    background:url("#{$img_url}icon_login_user.png") no-repeat;
  }
  .glyphicon-lock:before {
    content:'';
    display:inline-block;
    width:14px;
    height:14px;
    background:url("#{$img_url}icon_login_lock.png") no-repeat;
  }
  .checkbox,
  .radio {
    margin-top:0;
    margin-bottom:0;
  }
  .btn-primary {
    margin-top:40px;
    padding-top:27px;
    padding-bottom:27px;
    font-size:1.8rem;
    border-color:#2a2a2a !important;
    background-color:#2a2a2a !important;
  }
}
.login-box-copy {
  font-size:1.2rem;
  color:#9c9fa6;
  text-align:center;
}

.main-header {
  border-bottom:3px solid #ddd;
  @include gnbBgColor1();
  &:after {
    content:'';
    display:block;
    width:100%;
    height:1px;
    background:#000;
  }
  .logo {
    .logo-lg {
      img {
        max-width:70%;
      }
    }
  }
}

.content-wrapper {
  background:initial;
}

.nav-stacked {
  > li {
    > a {
      font-weight:$r;
      border-left:0;
    }
    > a:hover,
    > a:focus {
      color:#fff;
      background:#333;
      border-left:0;
    }
    &.active {
      > a,
      > a:hover,
      > a:focus {
        font-weight:$r;
        color:#fff;
        background:#333;
        border-left:0;
      }
    }
  }
}

.btn:active,
.btn:focus {
  outline:none !important;
}

.input-group {
  font {
    display:table-cell;
    padding-left:10px;
    vertical-align:middle;
    span {
      color:#888;
    }
    #titleByte {
      color:#333;
    }
  }
  .input-group-txt {
    float:left;
    display:table-cell;
    line-height:$btn_h;
  }
}

.modal-header {
  padding:20px $modal_dflt_pd;
  border-bottom:1px solid #000;
  .modal-title {
    font-size:2.2rem;
    line-height:1;
    color:$default_fc;
  }
  .close {
    margin-top:0;
    font-size:0;
    opacity:1;
    &:before {
      content:'';
      display:inline-block;
      width:21px;
      height:21px;
      background:url('#{$img_url}btn-close.png') no-repeat 50%;
    }
  }
}
.modal-body {
  padding:30px $modal_dflt_pd 25px;
}
.modal-footer {
  padding:0 $modal_dflt_pd 26px;
  border-top:0;
}


