}
.table-grid-wrap {
  &.table-grid-2 {
    > .table {
      > thead,
      > tbody{
        > tr {
          > th {
            width: 20%;
          }

          > td {
            width: 80%;
          }
        }
      }
    }
  }
  &.table-grid-4 {
    > .table {
      > thead,
      > tbody {
        > tr {
          > th {
            width: 20%;
          }

          > td {
            width: 30%;
          }
        }
      }
    }
  }
  &.table-grid-6 {
    > .table {
      > thead,
      > tbody {
        > th {
          width: 10%;
        }
        > td {
          width: 20.33%;
        }
      }
    }
  }
}
.table-jqgrid-wrap {
  position:relative;
  .table {
    th {
      text-align:center;
    }
    td {
      text-align:center;
    }
  }
  input.editable {
    text-align:center;
  }
  .jqgrid-btn-wrap {
    position:relative;
  }
  &.use-pager {
    .jqgrid-btn-wrap {
      position:absolute;
      left:0;
      bottom:0;
    }
  }
}

