.table {
  clear:both;
  //margin-top:10px;
  margin-bottom:0;
  mini-height:50px;
  border-top:1px solid $td_bd_clr;
  border-right:1px solid $td_bd_clr;
  border-left:1px solid $td_bd_clr;
  border-top:1px solid #000;
  > thead, > tbody, > tfoot {
    > tr {
      > th, > td {
        /*padding:9px;*/
        padding:8px 20px;
        vertical-align:middle;
        border-top:0 !important;
        border-bottom: 1px solid $td_bd_clr !important;
      }
      > th {
        width:15%;
        padding-left:2rem;
        font-weight:$r;
        font-size:1.3rem;
        color:$cntn_fc_h2;
        background-color: $th_bg_clr;
        label {
          margin-bottom:0;
          font-weight:$r;
          color:$cntn_fc_h2;
        }
      }
    }
  }
  > thead {
    > tr {
      > td {
        text-align:center;
        background-color: #f0f0f0;
        &.text-right {text-align:right;}
        &.text-left {text-align:left;}
        &.text-center {text-align:center;}
      }
      &:last-child > th {
        background-color:#F5F5F5;
      }
    }
  }
  th,
  select {
    height:$btn_h;
  }
  td {
    &[colspan*="2"],
    &[colspan*="3"] {
      width:auto !important;
    }
    div[class*="col-"] {
      padding-left:0;
      padding-right:0;
    }
    div[class*="col-md-4"] {
      padding-right:0;
    }
    div[class*="col-md-pd-l"] {
      padding-left:10px;
    }
    .radio-inline {
      padding-top:8px;
      padding-bottom:8px;
    }
    .t-txt {
      display:inline-block;
      padding-top:8px;
      padding-bottom:8px;
    }
    .td-list-grid-wrap {
      padding-top:10px;
      .option-btnbox.mt0 {
        margin-bottom: 10px;
      }
      .has-pagenation {
        &.left {
          left:0;
        }
        &.right {
          right:0;
        }
      }
    }
  }
  .empty-td {
    border-right:0;
    background:#fff;
    & + td {border-left:0;}
  }
  &.reset-size {
    > tbody {
      > tr {
        > th,
        > td {
          width: auto;
        }
      }
    }
  }
}

.table-line {
  padding: 20px;
  border: 1px solid $th_bd_clr;
}
.view-table {
  border-bottom:0;
  tr:first-child {
    th,
    td {border-top:0;}
  }
  th {width:100px;}
}

.list-table {
  th {
    width:auto;
    text-align:center;
    border-bottom:2px solid $th_bd_clr;
  }
  td {
    text-align:center;
  }
}

.write-table {
  width:100%;
  border-top:1px solid $border_clr;
  border-right:1px solid $border_clr;
  border-left:1px solid $border_clr;
  th,
  td {
    padding:5px 15px;
    border-bottom:1px solid $border_clr;
  }
  th {
    line-height:$btn_h;
    background:$th_bg_clr;
  }
  div[class*="col-"] {
    float:left;
    padding-right:5px;
    padding-left:5px;
    overflow:hidden;
  }
}

.write-table2 {
  width:100%;
  border-top:2px solid $border_clr;
  border-right:1px solid $border_clr;
  // border-left:1px solid $border_clr;
  th,
  td {
    padding:5px 0;
    border-bottom:1px solid $border_clr;
    border-left:1px solid $border_clr;
    text-align:center;
  }
  th {

    line-height:$btn_h;
    background:$th_bg_clr;
  }
  div[class*="col-"] {
