    padding-right:5px;
    padding-left:5px;
    overflow:hidden;
  }
}

.table-search {
  .table {
    border-top: 2px solid $th_bd_clr;
    border-right: 1px solid $td_bd_clr;
    border-bottom: 0;
    border-left: 1px solid $td_bd_clr;
    margin-bottom: 16px;
    .tr {
      display: table;
      width: 100%;
      &:after {
        clear: both;
        content: '';
        display: block;
      }
      & > div[class*="col-md"] {
        border-bottom: 1px solid $td_bd_clr;
        float: initial;
        display: table-cell;
      }
      & > div[class*="col-md-2"] {
        background: $th_bg_clr;
      }
      .th,
      .td {
        min-height: 40px;
        height: auto;
      }
      .th {
        padding-right: 18px;
        padding-left: 18px;
        line-height: 40px;
        background-color: $th_bg_clr;
      }
      .td {
        padding:5px 15px;
        & div[class*="col-"] {
          padding-right:5px;
          padding-left:5px;
          .chck_label_wrap {
            margin-top:5px;
          }
        }
      }
    }
    .input-group.date {
      float: left;
      width: 46%;
    }
    .input-group-addon {
      padding: 4px 12px;
    }
    .input-group-gubun {
      float: left;
      padding: 0;
      width: 8%;
      line-height: 30px;
      text-align: center;
    }
    .btn-search {
      line-height: 1 !important;
      background: #ffffff;
      border: 1px solid $td_bd_clr;
      border-left: none;
      width: 100%;
      padding: 0 13px;
    }
  }
  .form-control {
    display: block;
    width: 100%;
    height: 30px;
    padding: 6px 12px;
    font-size: 12px;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid $td_bd_clr;
    border-radius: 0;
  }
}

.search-table {
  margin-bottom:0;
  .table {
    border-bottom:none;
    > tbody {
      > tr {
        > th {
          width:15%;
        }
        > td {
