      border-top: none;
      margin-top: -20px;
    }
    .tr {
      width: 100%;
      &:after {
        clear: both;
        content: '';
        display: block;
      }
      & > div[class*="col-md"] {
        border-bottom: 1px solid $td_bd_clr;
        float: initial;
      }
      & > div[class*="col-md-2"] {
        background: $th_bg_clr;
      }
      .th,
      .td {
        min-height: 40px;
        height: auto;
      }
      .th {
        padding-top: 11px;
        padding-bottom: 11px;
        padding-right: 10px;
        background-color: $th_bg_clr;
      }
    }
  }
}

