.table-stat {
  table {
    border: 1px solid $td_bd_clr;
    border-top: 1px solid #999999;
    thead {
      tr {
        th {
          height: 40px;
          background-color: #f0f0f0;
          border-bottom: 1px solid $td_bd_clr;
          border-right : 1px solid $td_bd_clr;
        }
        td {
          height: 40px;
          background-color: #f7f7f7;
        }
      }
    }
    tbody {
      tr {
        .basic {
          color: #666 !important;
          font-weight: normal !important;
          background-color: #ffffff !important;
        }
      }
    }
  }
  .border-r {border-right : 1px solid $td_bd_clr;}
  .border-r-none {border-right : none;}
  .total-row-all td {background-color: #e7edf9; color: #e42e67;}
  .total-col {background-color: #e7edf9; color: #e42e67;}
  .sum-row-all td {background-color: #f4f7fc; color: #df114f;}
  .sum-col {background-color: #f4f7fc;color: #df114f;}
  .color-red {color: #e42e67;}
}

ul.table-collapse{
  border-top:1px solid $border_clr;
  margin-bottom:0;
  padding:0;
  list-style:none;
  .form-group{margin-bottom:0;}
  .checkbox-inline{padding-top:0;}
  li{
    display: inline-block;
    height: 50px;
    line-height: 50px;
    padding-right:20px;
    padding-left:20px;
    padding-bottom:9px;
    border-top:1px solid $border_clr;
    select{margin-top:9px;}
    *:first-child{
      padding-left:0;
      &.btn{padding-left:29px;}
      &[type='text']{padding-left:12px;}
    }
    &:nth-child(odd){
      background-color:$th_bg_clr;
      font-weight: 700;
    }
  }
}
