//function
@function str-replace($string, $search, $replace: '') {
	$index: str-index($string, $search);
	@if $index {
		@return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
	}
	@return $string;
}

//mixin
@mixin gnbBgColor1() {background-color:$gnb_bg_clr1;}
@mixin gnbBgColor1_hover() {background-color:$gnb_bg_clr1_hover;}
@mixin lnbBgColor1() {background-color:$lnb_bg_clr1;}
@mixin lnbBgColor1_hover() {background-color:$lnb_bg_clr1_hover;}
@mixin lnbSubBgColor1() {background-color:$lnb_sub_bg_clr1;}

@mixin f_bgimg($ivar) {
	background-image:url("#{$lnbx1_icon_img_url}#{$ivar}");
}
@mixin f_bgimg_hover($ivar) {
	$ivar_over:str-replace($ivar,'.png','_over.png');
	background-image:url("#{$lnbx1_icon_img_url}#{$ivar_over}");
}
@mixin f_a_tag() {
	&:hover, &:active, &:focus {
		color:$gnb_fc_hover;
		background-color:$gnb_bg_clr1_hover;
	}
}

@mixin f_lnb_idrm_icon_make(){
	@each $ilt, $ilu in $array_i_lnb_data {
		.idrm-icon-#{$ilt} {
			@include f_bgimg($ilu);
		}
	}
}

@mixin f_lnb_idrm_icon_make_hover(){
	@each $ilt, $ilu in $array_i_lnb_data {
		.idrm-icon-#{$ilt} {
			@include f_bgimg_hover($ilu);
		}
	}
}
@mixin f_tit_icon_make(){
	@each $ilt, $ilu in $array_i_tit_data {
		&.idrm-icon-#{$ilt} {
			background-image:url("#{$tit_icon_img_url}#{$ilu}");
			background-repeat:no-repeat;
			background-size:contain;
			background-position:0 100%;
		}
	}
}