/* jqgrid  */
.ui-jqgrid.ui-widget.ui-widget-content.ui-corner-all,
.ui-jqgrid-view,
.ui-jqgrid-hdiv,
.ui-jqgrid-htable,
.ui-jqgrid-bdiv,
.ui-jqgrid-btable,
.ui-jqgrid-pager {width:100% !important;}
/*.ui-jqgrid .ui-jqgrid-hbox {width:100% !important;padding:0 !important;}*/

/* jqgripd design custom */
.ui-row-ltr.ui-state-highlight {
  td {
    background:#eee;
  }
}
.ui-jqgrid {
  .ui-jqgrid-btable {
    margin-bottom:25px;
  }
  .ui-pg-input,
  .ui-jqgrid-toppager .ui-pg-input {
    text-align:center;
    border:0;
    + span {
      display:inline-block;
      width:35px;
      text-align:center;
    }
  }
  .ui-jqgrid-pager,
  .ui-jqgrid-toppager {
    padding-top:5px;
  }
}
.jqgrid-no-data-txt {
  min-height:200px;
  display:flex;
  align-items:center;
  justify-content:center;
  span {
    font-size:16px;
  }
}

.list-cnt-txt {
  display:inline-block;
  font-size:12px;
  font-weight:400;
  em {
    padding-right:2px;
    padding-left:2px;
    font-style:normal;
    font-weight:700;
    //color:$point_clr;
    color:$txt_clr;
  }
}

/* pagination */
.pagination-wrap {
  text-align:center;
}
.pagination{
  margin:0 0 10px;
  @media (max-width:$md_max_w_m) {
    margin:0;
  }
  > li {
    > a,
    > span {
      /* 18.06.14  */
      /* padding:0;
      width:30px;
      height:30px;
      line-height:30px;
      text-align:center;
      background-color:#fff;
      color:$cntn_fc_c1; */
      &:focus, &:hover{
        background-color:$btn_bg_clr1;
        border-color:$btn_bg_clr1;
        color:$btn_fc;
      }
    }
  }
  > .active {
    > a,
    > span {
      background-color:#fff;
      border-color:$btn_bg_clr1;
      color:$btn_fc;
      &,
      &:focus,
      &:hover {
        background-color:$btn_bg_clr1;
        border-color:$btn_bg_clr1;
        color:$btn_fc;
      }
    }
  }
}

.pagination-sm {
  > li:last-child,
  > li:first-child {
    a,
    span {
      border-radius: 0;
    }
  }
}
/* reply */
.rpWriteBox {
  position:relative;
  padding:$cntn_pd;
  padding-right:$cntn_pd + 100px;
  width:100%;
  background:#eaedf2;
  textarea{
    display:inline-block;
    width:100%;
    height:100px;
  }
  button{
    position:absolute;
    top:$cntn_pd;
    right:$cntn_pd;
    width:100px;
    height:100px;
    margin-left:0;
    padding:0;
  }
}
.rpWriteBox.rpWriteBox2{
  margin-top:$cntn_pd;
  padding-bottom:0;
  padding-left:$cntn_pd * 2;
  background:url('#{$img_url}rp_line.png') no-repeat 18px 20px;
  border-top:1px solid $border_clr;
}
.rp-list-wrap {
  .rp-wrap-d1 {
    margin:0;
    padding:0;
    li {
      padding:$cntn_pd;
      padding-right:0;
      list-style:none;
      border-bottom:1px solid $border_clr;
      .rp-item {
        .rp-item-info {
          &.form-group {margin-bottom:5px;}
          .rp-date {
            margin:0 10px;
            color:#bbb;
          }
          .btn-rp {
            margin-right:0;
            color:$cntn_fc_c1;
            &:before {
              content:"|";
              padding:0 10px;
            }
            &.btn-rp1 {

              &:before {
                content:"";
                padding:0;
              }
            }
            &.btn-rp-point {color:$point_clr2;}
          }
        }
        .rp-cnts {

        }
        .rp-wrap-d2 {
          margin-top:$cntn_pd;
          margin-left:0;
          padding-left:0;
          border-top:1px solid $border_clr;
          li {
            padding-left:$cntn_pd * 2;
            background:url('#{$img_url}rp_line.png') no-repeat 18px 15px;
            &:last-child {
              padding-bottom:0;
              border-bottom:0;
            }
          }
        }
      }
    }
  }
}

.cardWrap {
  margin-bottom: 20px;
}

.card-box {
  margin-top: 16px;
  &:after {
    clear: both;
    content: '';
    display: block;
  }
  .thumbnail {
    &.card {
      border: 1px solid $td_bd_clr;
      padding: 22px 20px 28px;
      margin-bottom: 20px;
      min-height: 260px;
      border-radius: 0;
      -webkit-transition: all 0.2s ease-in-out 0s;
      transition: all 0.2s ease-in-out 0s;
      &:hover {
        background-color: #f1f4f9;
      }
      .card-body {
        min-height: 149px;
        .card-title {
          font-size: 1.3em;
          line-height: 1.5;
          font-weight: normal;
          padding-top: 11px;
        }
        .card-text {
          width: 100%;
          overflow: hidden;
          font-size: 1em;
          padding: 5px 0;
          margin: 0;
        }
      }
      &.display {
        text-align: center;
        padding: 15px 20px 22px;
        .card-top {
          padding-bottom: 15px;
          .label.display {
            display: inline-block;
          }
        }
        .card-body {
          padding-right: 10%;
          padding-left: 10%;
        }
      }
    }
  }
}
@media (min-width:$md_min_w_l){
  .lt-box-pad {
    padding-left: $cntn_pd !important;
    padding-right: ($cntn_pd / 2) !important;
  }
  .md-box-pad {
    padding-left: $cntn_pd !important;
    padding-right: $cntn_pd !important;
  }
  .rt-box-pad {
    padding-left: ($cntn_pd / 2) !important;
    padding-right: $cntn_pd !important;
  }
  .card-box .thumbnail.card.display .card-body {
    padding-right: 25%;
    padding-left: 25%;
  }
}
@media (min-width:$md_min_w_m){
  .card-box{
    margin-right: 0;
    padding: 0 15px;
    .col-sm-3 {
      padding-left: 5px;
      padding-right: 5px;
    }
    .thumbnail.card .card-body .card-text {
      padding: 5px 10px 5px 0;
    }
  }
  .table-view .table {
    .tbody {
      display: table;
      width: 100%;
    }
    .tr {
      display: table-row;
    }
    .th,
    .td {
      display: table-cell;
    }
  }
}
@media (max-width:$md_max_w_m){
  .table-search .table {
    .tr > div[class*="col-md"] {
      display: block;
    }
    .date-btn {
      width: 100%;
      .btn {
        width: 20%;
        max-width: 20%;
      }
    }
  }
  .btn-search {
    margin-bottom: 0;
  }
  .pc-only {
    display: none;
  }
  .card-box .thumbnail.card {
    min-height: auto;
    padding: 15px 10px;
    margin-bottom: 10px;
    .card-body {
      min-height: auto;
      .card-title {
        font-size: 14px;
      }
    }
    .card-top .btn-icon .fa-edit {
      font-size: 20px;
      color: #5e86c9;
    }
  }
}
@media (max-width:$md_max_w_s){
  .nav-tabs-custom > .tab-content {
    padding: 0;
  }
  .btn {
    &.btn-m1 {
      width: 100%;
    }
    &.btn-m2 {
      width: calc(50% - 3px);
    }
    &.btn-m3 {
      width: calc(33.333333% - 3px);
    }
  }
  .btnbox-r {
    text-align: justify;
  }
  .btnbox-l {
    text-align: justify;
    margin-bottom: 0;
  }
  .table-search {
    .table {
      .td {
        padding-left: 5px;
        padding-right: 5px;
      }
    }
  }
  .table-view {
    .table {
      .th {
        padding-left: 20px;
      }
      .td {
        padding-top: 10px;
        padding-left: 20px;
      }
    }
  }
  .modal .modal-dialog .modal-content {
    padding: 10px;
  }
  .card-box {
    .col-xs-6 {
      &:nth-child(odd) {
        padding-right: 5px;
      }
      &:nth-child(even) {
        padding-left: 5px;
      }
    }
    .thumbnail.card {
      .card-bottom {
        padding-top: 15px;
      }
      .card-body {
        .card-title {
          margin: 0;
          text-align: center;
        }
      }
    }
  }
  .btn-icon {
    display: block;
  }
}

.sle_stauts_label {
  display:inline-block;
  padding:0 5px 2px;
  min-width:56px;
