    background:#fff;
  }
}

.thumblist_all_check {
  position:relative;
  float:left;
  margin-right:5px;
  width:$btn_h;
  height:$btn_h;
  background:$border_clr;
  .icheckbox_minimal-blue {
    position:absolute;
    top:50%;
    left:50%;
    margin-top:-9px;
    margin-left:-9px;
  }
}

.list_type_ul {
  float:left;
  margin:0 5px 0 0;
  padding:0;
  width:auto;
  li {
    display:inline-block;
    width:30px;
    height:30px;
    &.ltu_thumb {
      a {
        background: url(../images/common/icon_list_thum.png) no-repeat 50%;
      }
      &.on {
        a {
          background: url(../images/common/icon_list_thum_on.png) no-repeat 50%;
        }
      }
    }
    &.ltu_list {
      a {
        background: url(../images/common/icon_list_list.png) no-repeat 50%;
      }
      &.on {
        a {
          background: url(../images/common/icon_list_list_on.png) no-repeat 50%;
        }
      }
    }
    a {
      display:block;
      width:100%;
      height:100%;
      span {
        font-size:0;
      }
    }
  }
}

.goods-multi-app {
  float:left;
  margin-right:5px;
  margin-bottom:5px;
  width:70px;
}

.list_type_box {
  display:none;
  &.list_type_thumb,
  &.list_type_list{
    &.on {
      display:block;
    }
  }
}

.thumbnail_list_wrap {
  margin:0;
  padding:0;
  width:100%;
  & > li {
    float:left;
    list-style:none;
    width:130px;
    & > a {
      display:inline-block;
      width:100%;
    }
  }
}

.thumb_list_item {
  margin:0;
  padding:0 5px;
  width:130px;
  height:250px;
  overflow:hidden;
  & > li {
    list-style:none;
  }
  .thm_gd_img {
    position:relative;
    width:120px;
    height:120px;
    border:1px solid $border_clr;
    overflow:hidden;
    .icheckbox_minimal-blue,
    .thm_gd_chck {
      position:absolute !important;
      top:10px;
      left:10px;
    }
    label,
    img {
      width:100%;
    }
  }
  .thm_gd_status {
    margin:10px 0;
  }
  .thm_gd_name {
    max-height:60px;
    overflow:hidden;
  }
  .thm_gd_name,
  .thm_gd_name span{
    color:$cntn_fc_h1;
    font-weight:600;
    font-size:14px;
  }
  .thm_gd_code {
    overflow:hidden;
  }
}
.list-sort-wrap {
  margin-bottom:$cntn_pd;
  padding:0;
  border:1px solid $th_bd_clr;
  h5 {
    margin:0;
    padding:15px;
    text-align:center;
    background:$th_bg_clr;
  }
  .list-sort-ul {
    margin:0;
    padding:0;
    width:100%;
    li {
      list-style:none;
      padding:10px;
      width:100%;
      text-align:center;
      border-top:1px solid $border_clr;
    }
  }
}

/* agGrid edit */
.ag-theme-alpine {
  .ag-root-wrapper {
    border:1px solid $th_bd_clr;
    border-top:1px solid #000;
  }
  .ag-header {
    border-bottom-color:$th_bd_clr;
  }
  .ag-cell {
    line-height:30px;
    text-align:center;
  }
}

/* toastGrid edit */
.list-grid-wrap {
  .list-grid {
    input[type=radio],
    input[type=checkbox] {
      @include css3-prefix('appearance','auto');
    }
  }
  &.responsive-grid {
    .tui-grid-container,
    .tui-grid-rside-area {
      width: auto !important;
    }
  }
}
div.tui-grid-container {
  font-family:$default_font;
  .tui-grid-cell {
    background:#fff;
  }
  .tui-grid-cell-header {
    background:#f0f0f0;
    font-weight:$r;
  }
}
div.tui-pagination {
  .tui-page-btn {
    margin-right:2px;
    margin-left:2px;
    padding:6px 0 8px;
    border-width:1px !important;
  }
  .tui-is-selected,
  strong {
    color:#fff;
    background:$point_clr !important;
    border-color:$point_clr !important;
  }
  .tui-prev {
    margin-right:10px !important;
  }
  .tui-next {
    margin-left:10px !important;
  }
  .tui-first + .tui-prev,
  .tui-next + .tui-last {
    border-left:1px solid #ddd !important;
  }
  .tui-first-child {
    &.tui-is-selected {
      border-left:1px solid $point_clr;
    }
  }
  .tui-first,
  .tui-prev,
  .tui-next,
  .tui-last,
  .tui-prev-is-ellip,
  .tui-next-is-ellip {
    padding:0;
  }
}
.tui-grid-border-line-top {
  border-top:1px solid #000 !important;

}
