/* layout s ============================================== */
.#{$skin_name} {
  .wrapper {
    @include lnbBgColor1();
    .main-header {
      .logo {
        //width:$sidebar_w;
        font-size:1.667em;
        line-height:$gnb_li_h;
        text-align:center;
        color:$gnb_fc;
        //@include lnbBgColor1_hover();
        border-bottom:0 solid transparent;
        //&:hover {@include gnbBgColor1_hover();}
        img {margin-bottom:2px;}
        @media (max-width:$md_max_w_s) {
          width:auto;
        }
      }
      .navbar {
        margin-left:$sidebar_w;
        @media (max-width:$md_max_w_s) {
          margin-left:0;
        }
        @include gnbBgColor1();
        .sidebar-toggle {
          position:absolute;
          left:0;
          display:inline-block;
          padding:0;
          //width:65px;
          //width:43px;
          width:$gnb_li_h;
          color:$gnb_fc;
          border-right:1px solid $border_clr_t;
          &:hover {
            color:$gnb_fc_hover;
            @include gnbBgColor1_hover();
          }
          &::before {
            content:'';
            display:inline-block;
            float:left;
            padding-left:4px;
            width:$sidebar_mini_w;
            height:$gnb_li_h;
            background:url('#{$img_url}#{$i_t_menu}') no-repeat 50%;
            background-size:contain;
          }
          @media (max-width:$md_max_w_s) {
            position:absolute;
            top:-$gnb_li_h;
            left:0;
          }
        }
        .navbar-custom-menu {
          float:right;
          //width:100%;
          text-align:center;
          .nav {
            &.navbar-nav-center,
            &.navbar-nav-right {
              float:none;
              display:inline-block;
            }
            &.navbar-nav-center {
              width:190px;
              text-align:center;
              > li {
                float:none;
                .idrm-icon {
                  width:35px;
                  height:30px;
                  &.idrm-icon-mail::before {content:url('#{$img_url}#{$i_t_mail}');}
                  &.idrm-icon-notice::before {content:url('#{$img_url}#{$i_t_notice}');}
                  &.idrm-icon-graph::before {content:url('#{$img_url}#{$i_t_graph}');}
                  &.idrm-icon-mall::before {content:url('#{$img_url}#{$i_t_mall}');}
                }

              }
            }
            &.navbar-nav-right {
              float:right;
              text-align:right;
              > li.user-menu {
                > a {
                  position:absolute;
                  right:0;
                  padding-top:13px;
                  width:120px;
                  img {height:25px;}
                  span {float:left;line-height:25px;}
                  @media (max-width:$md_max_w_m) {
                    right:70px;
                    padding-top:10px;
                    text-align:center;
                  }
                  @media (max-width:$md_max_w_s) {
                    right:0;
                    width:70px;
                  }
                  &:before {
                    content:'';
                    position:absolute;
                    top:50%;
                    right:15px;
                    margin-top:-2px;
                    width:8px;
                    height:5px;
                    background:url('#{$img_url}profile_arrow.png') no-repeat;
                  }
                }
                .dropdown-menu {
                  top:$gnb_li_h;
                }
              }
            }
            > li {
              display:inline-block;
              &:not(:first-child):after {
                content:'';
                position:absolute;
                top:50%;
                left:0;
                margin-top:-9px;
                width:1px;
                height:18px;
                background:rgba(0,0,0,.2);
              }
              > a {
                padding-top:8px;
                padding-bottom:10px;
                height:$gnb_li_h;
                line-height:30px;
                color:$gnb_fc;
                @include f_a_tag();
                > .label {
                  top:23px;
                  right:7px;
                  width:23px;
                  height:14px;
                  text-align:center;
                  font-size:0.813em;
                  padding:2px 3px;
                  line-height:1;
                  background-color:$point_clr !important;
                  border-radius:10px;
                }
              }
              > a .user-image {margin-top:0;}
              > a[data-toggle='control-sidebar'] {
                // width:70px;
                //width:41px;
                padding-left:35px;
                height:$gnb_li_h;
                line-height:$gnb_li_h;
                padding-top:0;
                padding-bottom:0;
                border-left:1px solid $border_clr_t;
                .idrm-icon-sidebarser::before {
                  position:absolute;
                  top:50%;
                  left:18px;
                  margin-top:-6px;
                  width:14px;
                  height:14px;
                  content:'';
                  background:url('#{$img_url}#{$i_t_setup}') no-repeat;
                }
                @media (max-width:$md_max_w_s) {
                  position:absolute;
                  top:-$gnb_li_h;
                  right:0;
                }
              }
              &.dropdown {
                .dropdown-toggle {}
                &.messages-menu {
                  a {
                    h4 {
                      font-weight:bold;
                    }
                    &:hover,
                    &:hover h4,
                    &:hover h4 small,
                    &:hover p {color:$lnb_fc_hover;@include gnbBgColor1_hover();}
                  }
                }
                &.notifications-menu {
                  .menu {
                    a {
                      background-color:$lnb_fc;
                      .fa {color:$lnb_bg_clr1_hover !important;}
                      &:hover {color:$lnb_fc_hover;@include gnbBgColor1_hover();}
                      &:hover .fa {color:$lnb_fc_hover !important;}
                    }
                  }
                }
                &.tasks-menu {
                  .menu {
                    li a {
                      h3 {font-weight:bold;}
                      &:hover {@include gnbBgColor1_hover();}
                      &:hover h3,
                      &:hover h3 small {color:$lnb_fc_hover !important;}
                      .progress-bar {@include gnbBgColor1_hover();}
                      &:hover .progress-bar {@include gnbBgColor1();}
                    }
                  }
                }
                &.user-menu {
                  .menu {
                  }
                  li.user-header {
                    height:auto;
                    font-weight:bold;
                    @include gnbBgColor1_hover();
                    > img {border:0;}
                    p {
                      font-weight:bold;
                      color:$lnb_fc_hover;
                    }
                  }
                  li.user-body {
                    font-weight:bold;
                    &:hover {color:$cntn_fc_h1;}
                  }
                  li.user-footer {
                    background:$footer_bg_clr;
                    a{color:#fff;}
                  }
                }
                .dropdown-menu {
                  position:absolute;
                  left:auto;
                  // padding:0;
                  border:1px solid #ddd;
                  background:#fff;
                  @media (max-width:$md_max_w_m) {
                    top:$gnb_li_h;
                    right:0;
                    width:100%;
                    border:0;
                  }
                  @media (max-width:$md_max_w_m) {
                    li {
                      &.divider {background-color: rgba(255, 255, 255, 0.1);}
                      a {
                        color:$gnb_fc;
                        &:hover {background-color:$lnb_bg_clr1_hover !important;}
                      }
                      &.user-body a:hover {background:#fff !important;}
                    }
                  }
                  li.footer a:hover {color:$lnb_fc_hover !important;@include gnbBgColor1_hover();}
                }
              }
            }
            .open {
              > a {@include f_a_tag();}
            }
            > .active > a {
              color:$gnb_fc_hover;
              @include gnbBgColor1_hover();
            }
          }
        }
      }
    }
    .left-side,
    .main-sidebar {
      width:$sidebar_w;
      height:100%;
    }
    .main-sidebar {
      padding-top:$gnb_li_h + 20px;
      @include lnbBgColor1();
      overflow-x:hidden;
      overflow-y:auto;
      .sidebar {
        a {
          color:$lnb_fc;
          &:hover {text-decoration:none;}
        }
        //noinspection CssInvalidPseudoSelector
        .sidebar-form {
          border-radius:3px;
          border:1px solid $main_bg_color_l_3;
          margin:30px 15px 10px 15px;
          input[type="text"],
          .btn {
            box-shadow:none;
            background-color:$main_bg_color_l_3;
            border:1px solid transparent;
            height:$btn_h + 5px;
            color:$lnb_fc_hover;
            border-top-left-radius:0;
            border-top-right-radius:2px;
            border-bottom-right-radius:2px;
            border-bottom-left-radius:0;
          }
          input[type="text"] {
            color:$lnb_fc_hover;
            border-top-left-radius:2px;
            border-top-right-radius:0;
            border-bottom-right-radius:0;
            border-bottom-left-radius:2px;
            &:placeholder {color:$lnb_fc_hover;}
            &:focus,
            &:focus + .input-group-btn .btn {
              background-color:#fff;
              color:#666;
            }
            &:focus + .input-group-btn .btn {
              border-left-color:#fff;
            }
          }
          .btn {
            padding:0 10px;
            margin-bottom:0;
            &:hover {@include lnbBgColor1_hover()}
            .idrm-icon-search::before {
              content:url('#{$img_url}#{$i_search}');
              line-height:$btn_h + 10px;
            }
          }
        }
        .sidebar-menu {
          > li.header {
            padding:0;
            //color:$lnb_fc_hover;
            @include lnbBgColor1();
            a {
              font-weight:bold;
              color:inherit;
            }
          }
          > li {
            > a {
              padding-top:0;
              padding-bottom:0;
              padding-left:$lnb_d1_pd_l;
              height:$lnb_li_h;
              line-height:$lnb_li_h;
              font-size:1.4rem;
              border-left:4px solid transparent;
              .idrm-icon {
                position:absolute;
                top:50%;
                left:$lnb_d1_pd_l;
                margin-top:-10px;
                width:18px;
                height:20px;
                background-repeat:no-repeat;
                background-size:contain;
                background-position:0 50%;
              }
              @include f_lnb_idrm_icon_make(); //lnb icon draw
              .idrm-icon + span {padding-left:$lnb_d1_pd_l + 12px;}
              > .pull-right-container {
                //right:25%;
                right:$lnb_d1_pd_l + 14px;
                margin-top:8px;
                //line-height:11px;
                .idrm-icon {background-size:contain;}
                .idrm-icon-arrow {
                  left:0;
                  display:inline-block;
                  width:9px;
                  height:6px;
                  @include f_bgimg($b_arrow_toggle);
                  background-repeat:no-repeat;
                }
              }
            }
            &:hover,
            &.active {
              > a {
                color:$lnb_fc_hover;
                @include lnbBgColor1_hover();
                //border-left-color:$lnb_fc_hover;
                @include f_lnb_idrm_icon_make_hover(); //lnb icon draw
              }
            }
            &:hover {
              > a .pull-right-container .idrm-icon-arrow {
                //@include f_bgimg_hover($b_arrow_right);
              }
            }
            &.active {
              > a .pull-right-container .idrm-icon-arrow {
                @include f_bgimg_hover($b_arrow_toggle);
              }
            }
            > .treeview-menu {
              margin:-10px 0 0 0;
              padding:15px 0;
              //@include gnbBgColor1_hover();
              > li {
                /*
                @include f_bgimg($b_lnb_dot_line);
                &.active,
                &:hover {@include f_bgimg_hover($b_lnb_dot_line);}
                &:first-child {@include f_bgimg($b_lnb_dot_line-b);}
                &.active:first-child,
                &:hover:first-child {@include f_bgimg_hover($b_lnb_dot_line-b);}
                &:last-child {@include f_bgimg($b_lnb_dot_line-t);}
                &.active:last-child,
                &:hover:last-child {@include f_bgimg_hover($b_lnb_dot_line-t);}
                &:only-child {@include f_bgimg($b_lnb_dot);}
                &:only-child.active,
                &:only-child:hover {@include f_bgimg_hover($b_lnb_dot);}
                */
                background-position:($lnb_d2_pd_l - 20px) 50%;
                background-repeat:no-repeat;
                a {
                  padding:0 5px 0 $lnb_d2_pd_l;
                  line-height:$lnb_d2_li_h;
                  color:$lnb_fc;
                  font-size:12px;
                  > .fa {display:none;}
                }
                &.active > a,
                > a:hover {
                  color:$lnb_fc_hover;
                }
              }
            }
          }
        }
      }
    }
    .control-sidebar {

    }
    .control-sidebar-bg {

    }
    .content-wrapper {
      margin-left:$sidebar_w;
      @media (max-width:$md_max_w_s) {
        margin-left:0;
      }
      background-color:$cntn_bg_clr2;
      overflow:hidden;
      &.white-page {background-color:$cntn_bg_clr2;}
      .content-header {
        // padding:$cntn_pd $cntn_pd 0 $cntn_pd;
        padding:24px ($cntn_pd * 2) 0 ($cntn_pd * 2);
        background:transparent;
        @media (max-width:$md_max_w_s) {
          padding:$cntn_pd_m $cntn_pd_m 0 $cntn_pd_m;
        }
        .breadcrumb {

        }
      }
      .content {
        // padding:($cntn_pd * 2) $cntn_pd;
        padding:$cntn_pd ($cntn_pd * 2) ($cntn_pd * 2) ($cntn_pd * 2);
        @media (max-width:$md_max_w_s) {
          padding:($cntn_pd_m * 2) $cntn_pd_m;
        }
      }
    }
    .right-side {
      margin-left:$sidebar_w;
    }
    .main-footer {
      margin-left:$sidebar_w;
      @media (max-width:$md_max_w_s) {
        margin-left:0;
      }
      padding:15px;
      font-size:1em;
      color:$cntn_fc_c1;
      text-align:center;
      border-top:1px solid darken($fntaws_fc,4%);
      background:$footer_bg_clr;
      .container {
      }
    }
  }

  /*  */
  &.sidebar-mini.sidebar-collapse {
    @media (min-width:$md_min_w_m) {
      .wrapper {
        .main-header {
          .logo {
            width:$sidebar_mini_w;
          }
          .navbar {
            //margin-left:$sidebar_mini_w;
            margin-left:0;
            .sidebar-toggle {

            }
            .navbar-vustom-menu {
              .nav {
                &.navbar-nav-center {

                }
                &.navbar-nav-right {

                }
              }
            }
          }
        }
        .main-sidebar {
          width:$sidebar_mini_w !important;
          .sidebar {
            a {

            }
            .sidebar-form {
              .btn {

              }
            }
            .sidebar-menu {
              > li {
                > a {
                  padding:0;
                  // height:$sidebar_mini_h;
                  height:($sidebar_mini_h - 10px);
                  line-height:($sidebar_mini_h - 10px);
                  text-align:center;
                  border-top:1px solid $border_clr_t;
                  border-left:0;
                  border-bottom:0;
                  border-right:0;
                  > .fa {
                    position:absolute;
                    top:50%;
                    left:50%;
                    margin-top:-13px;
                    margin-left:-15px;
                    font-size:2.4em;
                  }
                  .idrm-icon {
                    top:50%;
                    left:50%;
                    // width:$sidebar_mini_w / 3;
                    // height:$sidebar_mini_w / 3;
                    //width:($sidebar_mini_w / 3) - 5px;
                    //height:($sidebar_mini_w / 3) - 5px;
                    // margin-top:-(($sidebar_mini_w / 3) / 2);
                    // margin-left:-(($sidebar_mini_w / 3) / 2) - 4px;
                    margin-top:-(($sidebar_mini_w / 3) / 2);
                    margin-left:-(($sidebar_mini_w / 3) / 2);
                  }
                  > span {text-align:left;}
                }
                &:last-child > a {border-bottom:1px solid $border_clr_t;}
                &:hover {
                  > a {border-right:1px solid $lnb_bg_clr1_hover;}
                  > a > span {top:0;padding-top:0;padding-bottom:0;font-weight:bold;font-size:1.333em;text-align:left;}
                  > a > span:not(.pull-right) {left:$sidebar_mini_w + 3px;}
                  > .treeview-menu {
                    padding:0;
                    top:$sidebar_mini_h;
                    left:$sidebar_mini_w;
                    > li {
                      background:none;
                      &.active,
                      &:hover {
                        @include gnbBgColor1();
                        font-weight:bold;
                        //border-left:2px solid $lnb_fc_hover;
                        a {
                          @include f_bgimg($b_arrow_right);
                          background-repeat:no-repeat;
                          background-position:90% 50%;
                        }
                      }
                      > a {
                        padding:0 5px 0 $lnb_d1_pd_l + 5px;
                      }
                    }
                  }
                  .pull-right-container {
                    //display:none;
                    top:50% !important;
                    margin-top:0px;
                    .idrm-icon {
                      &.idrm-icon-arrow {
                        margin-top:-5px;
                        @include f_bgimg_hover($b_arrow_down);
                      }
                    }
                  }
                }
                > .treeview-menu {
                  > li {

                  }
                }
              }
            }
          }
        }
        .control-sidebar {

        }
        .control-sidebar-bg {

        }
        .content-wrapper {
          margin-left:$sidebar_mini_w !important;
          .content-header {

          }
          .content {

          }
        }
        .main-footer {

        }
      }
    }
  }
  &.layout-top-nav {
    .content-wrapper,
    .right-side,
    .main-footer {
      margin-left:0;
    }
    .content {
      padding:($cntn_pd * 2) ($cntn_pd * 3);
      @media (max-width:$md_max_w_s) {
        padding:($cntn_pd_m * 2) ($cntn_pd_m * 2);
      }
    }
  }
}
/* layout e ============================================== */
