/* simple s ================================================= */

/* position */
.pstS {position:static !important;}
.pstR {position:relative !important;}
.pstA {position:absolute !important;}
.pstF {position:fixed !important;}

/* float */
.fL {float:left !important;}
.fR {float:right !important;}
.fN {float:none !important;}
.cB {clear:both !important;}

/* display */
.dN {display:none !important;}
.dB {display:block !important;}
.dIB {display:inline-block !important;}
.dI {display:inline !important;}

/* margin */
@for $step_size from 0 through 100 {
	@if $step_size == 0 {
		.mt#{$step_size} {margin-top: #{$step_size} !important;}
		.mr#{$step_size} {margin-right: #{$step_size} !important;}
		.mb#{$step_size} {margin-bottom: #{$step_size} !important;}
		.ml#{$step_size} {margin-left: #{$step_size} !important;}
	}
	@else {
		.mt#{$step_size} {margin-top: #{$step_size}px !important;}
		.mr#{$step_size} {margin-right: #{$step_size}px !important;}
		.mb#{$step_size} {margin-bottom: #{$step_size}px !important;}
		.ml#{$step_size} {margin-left: #{$step_size}px !important;}
	}
}

/* padding */
@for $step_size from 0 through 100 {
	@if $step_size == 0 {
		.pt#{$step_size} {padding-top:#{$step_size} !important;}
		.pr#{$step_size} {padding-right:#{$step_size} !important;}
		.pb#{$step_size} {padding-bottom:#{$step_size} !important;}
		.pl#{$step_size} {padding-left:#{$step_size} !important;}
	}@else {
		.pt#{$step_size} {padding-top:#{$step_size}px !important;}
		.pr#{$step_size} {padding-right:#{$step_size}px !important;}
		.pb#{$step_size} {padding-bottom:#{$step_size}px !important;}
		.pl#{$step_size} {padding-left:#{$step_size}px !important;}
	}
}
