/* font | em, rem */
$dotdiv:'.';
@for $step_size from 0 through 5 {
	@for $step_size2 from 1 through 9 {
		.fs#{$step_size}#{$step_size2}e {font-size:#{$step_size}#{$dotdiv}#{$step_size2}em !important;}
		.fs#{$step_size}#{$step_size2}r {font-size:#{$step_size}#{$dotdiv}#{$step_size2}rem !important;}
	}
}

