.ellipsis {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.line-division {
  border-top: 1px solid #555555;
  margin-bottom: 20px;
}
.line-dot-hr {
  margin:60px 0 0 0;
  width:100%;
  height:3px;
  background:url("#{$img_url}dot-line.png");
  border:0;
}
.point_clr {color:$point_clr !important;}
.gnbBgColor1 {@include gnbBgColor1();}
.gnbBgColor1_hover {@include gnbBgColor1_hover();}
.lnbBgColor1 {@include lnbBgColor1();}
.lnbBgColor1_hover {@include lnbBgColor1_hover();}
.lnbSubBgColor1 {@include lnbSubBgColor1();}
.option-box{
  padding: 0;
  text-align: left;
  margin-bottom: 20px;
  > li{
    float: left;
    list-style: none;
    margin-right: 10px;
    > *{
      margin-right: 5px;
      display: inline-block;
      width: auto;
    }
    &:last-child{
      float: right;
      margin-right: 0;
      margin-left:7px;
    }
  }
}
.readonly,
.readonly input,
.readonly .btn {
  background: #f6f6f6 !important;
}
.point-clr {color:$point_clr;}
.pre-box {
  white-space: pre-line;
}
.toggle-itm {
  &.tgl-hide,
  &.tgl-sub-hide {display:none;}
}
/* easyui */
.tooltip {display:none !important;}

.content-wrapper {
  .content-header {
    background-color:$cntn_bg_clr2 !important;
    border-bottom: 1px solid $border_clr;
    h1 {
      color:$cntn_fc_h1;
      font-weight:500;
      font-size: 3.2rem;
      padding-bottom: 16px;
      //border-bottom: 1px solid $border_clr;
    }
    .breadcrumb {
      top:auto;
      bottom:6px;
      right:($cntn_pd * 2);
      padding-right:0;
      @media (max-width:$md_max_w_m){
        right:0;
        background:none;
        display:none;
      }
      @media (max-width:$md_max_w_s){
        display:none;
      }
      li {
        line-height:19px;
        a {
          vertical-align:top;
        }
        &:first-child {
          padding-left: 15px;
          a {
            .fa.fa-dashboard {
              display:none;
              position: absolute;
              top:12px;
              left:6px;
              &::before {
                content: url('#{$img_url}#{$i_bread-home}');
              }
            }
          }
        }
        &:last-child {
          color:#000;
        }
      }
      li + li::before {
        color:$cntn_fc_c1;
      }
    }
    .breadcrumb02 {
      float: left;
      padding: 7px 2px;
      list-style: none;
      background: none;
      li {
        display: inline;
        &+li:before {
          padding: 8px;
          content: ">\00a0";
        }
      }
    }
  }
  .content {
    .subpage-title {
      float:left;
      margin-top:$block-mt;
      margin-bottom:15px;
      font-weight:$r;
      font-size:2.2rem;
      color:$cntn_fc_h2;
      & + div[class*=row] {
        clear:both;
      }
      & + .option-btnbox {
        //margin-top:20px;
      }
      &:first-child {
        //margin-top:0 !important;
        & + .jqgrid-top-btn-wrap {
          //margin-top:0 !important;
        }
      }
    }
    .col-md-12 {
      //margin-bottom:60px;
      .jqgrid-top-btn-wrap {
        //margin-top: 25px;
      }
    }
    .pointBlt {
      display:inline-block;
      //margin-right:5px;
      width:16px;
      height:24px;
      //border-right:5px solid $main_bg_color;
      background:url('#{$img_url_tit}icon_tit_dot.png') no-repeat 0 60%;
      vertical-align:bottom;
    }
    .box {
      padding:$cntn_bx_pd;
      border:1px solid $border_clr;
      //border-top:0;
      box-shadow:none;
      .box-header {
        padding:0 0 30px;
      }
      .box-title {
        padding-bottom:10px;
        width:100%;
        color:$cntn_fc_h1;
        font-weight:bold;
        font-size:1.333em;
        border-bottom:2px solid $border_clr;
        .pointBlt {
          background-position-y:80%;
        }
      }
      &.collapsed-box {
        .box-header {
          padding-bottom:0;
          .box-title {
            padding-bottom:0;
            border-bottom:0;
          }
        }
      }
      .box-tools {
        right:0;
        top:-5px;
        .fa{
          color: $fntaws_fc;
          font-size:2em;
        }
      }
      .box-body {
        padding:0;
        &.box-border-top {
          border-top:2px solid $border_clr;
        }
        .form-group {
          label {
            color:$cntn_fc_h2;
            margin-bottom:10px;
          }
          .form-control {
            color:$cntn_fc_c1;
            font-size:1em;

          }
        }
        &.table-responsive {
          border-top:0;
        }

      }
      .box-footer {
        padding:10px 0;
        &.btn-form-wrap {
          padding-top:25px;
        }
      }
      @media (max-width:$md_max_w_m){
        .nav-tabs {
          margin-right:0;
          margin-left:0;
        }
      }
      @media (max-width:$md_max_w_s){
        .box {
          // margin-right:-$cntn_bx_pd;
          // margin-left:-$cntn_bx_pd;
        }
      }
    }
    .subbox {
      .subbox-title {
        float:left;
        width:100%;
        color:$cntn_fc_h1;
        font-weight:bold;
        font-size:1.333em;
        padding-bottom:20px;
        border-bottom:2px solid $border_clr;
      }
    }
    .radio-inline {
      @media (max-width:$md_max_w_m){
        margin-bottom: 5px;
      }
    }
    .btn-wrap {
      //margin-bottom:$cntn_pd;
    }
  }
  .ppp-content-wrap {
    padding:0 !important;
    .content-header {
      padding-top:17px !important;
      padding-bottom:18px !important;
      background:#444 !important;
      h1 {
        color:#fff;
        font-weight:400;
        font-size:2.2rem;
        padding-bottom:0;
      }
    }
    .nav-tabs-custom {
      margin-top:40px;
    }
    .ppp-content {
      padding:40px;
    }
    .ppp-btn-wrap {
      clear:both;
      text-align:center;
    }
  }
}
.jqgrid-top-btn-wrap,
.option-btnbox,
.option-btnbox-btm {
  &.type2 {
    margin-top:0;
  }
  .form-inline {
    display:inline-block;
    label {
      font-weight:$r;
    }
  }
  .form-control {
    height:$btn_h2;
  }
  .control-label {
    margin-right:5px;
    line-height:$btn_h;
  }
  .btn {
    @media (max-width:$md_max_w_s){
      padding:0 10px;
    }
    padding:5px 25px;
    font-size:12px;
    line-height:1.5;
    min-width:auto;
    height:$btn_h2;
    // line-height:$btn_h;
    margin-left:8px;
    &:first-child {
      margin-left:0;
    }
  }
  select {margin-left:5px;display:inline-block;width:auto;}
}
.option-txtbox {
  display:inline-block;
  font-weight:bold;
  line-height:$btn_h;
}
.jqgrid-top-btn-wrap,
.option-btnbox {
  margin-top:$block-mt;
  float:right;
  text-align:right;

}
.option-btnbox-btm {
  margin-top:20px;
  &.left {
    float:left;
    text-align:left;
  }
  &.right {
    float:right;
    text-align:right;
  }
  &.has-pagenation {
    position:absolute;
    bottom:11.5px;
    &.left {
      left:$cntn_pd;
    }
    &.right {
      right:$cntn_pd;
    }
  }
}
.GoodsImg {
  width: 130px;
  padding: 10px 15px;
  display: inline-block;
  .UploadImg {
