.dash-Wrap {
  margin-right:0;
  margin-left:0;
}
.dash {
  .box-header{
    margin-bottom: -26px;
  }
  .box-title {
    border-bottom: none;
  }
  table {
    border-bottom: none;
  }
  .box-body {
    .order_wrap {
      box-sizing: border-box;
      & > div {
        padding: 18px 25px;
        @for $i from 1 through 8 {
          .icon0#{$i} {background-image: url(#{$img_url}dashboard_icon_00#{$i}.png);}
          &:hover {
            .icon0#{$i} {background-image: url(#{$img_url}dashboard_icon_00#{$i}_over.png);}
          }
        }
        h2 {
          margin: 0;
          color: #5e8dd5;
        }
        h6 {
          margin-bottom: 6px;
        }
        &:hover {
          background-color:#5c93ee;
          box-shadow: 5px 5px 10px #ddd;
          h2,
          h6 {
            color: #ffffff;
          }
        }
        .icon{
          width: 50px;
          height: 40px;
          margin: 0 auto;
          background-position : center;
          background-repeat: no-repeat;
        }
      }
    }
    .order_box01 > div {border: 1px solid #ddd;}
    .order_box02 > div {border: 1px solid #ddd;border-top: none;}
    .dash-tab {margin-top:-28px;}
  }
  .QnA {
    @media (max-width:$md_max_w_m){
      padding-top:20px;
    }
    .tableWrap {
      border: 1px solid #ddd;
      .table {
        margin-bottom: 34px;
        border-bottom: none;
        td {
          height:53px;
          border-left:0;
          border-right:0;
        }
      }
    }
  }
  .main_wrap {
    & + .row {
      clear: both;
    }
    & > div {
      margin-bottom:30px;
      padding: 25px;
      border: 1px solid #ddd;
      @media (max-width: 1390px) {
        width: 50%;
      }
      @media (max-width: 1100px) {
        width: 100%;
      }
      &:hover {
        background-color: #f8f8f8;
        cursor: pointer;
      }
      .col-sm-8 {
        padding-top: 4%;
      }
      .icon{
        width: 80px;
        height: 80px;
        margin: 0 auto;
        border-radius: 80px;
        background-position: center;
        background-repeat: no-repeat;
      }
      @for $i from 1 through 4 {
        .icon0#{$i} {
          background-image: url(#{$img_url}dashboard_icon_0#{$i}.png);
        }
      }
      .text {
        margin: 0 auto;
        border-radius: 20px;
        color: #ffffff;
        height: 22px;
        padding: 3.5px 0;
        width: 19%;
        min-width: 41px;
        display: block;
      }
      .text03,
      .text04 {width: 32%; min-width: 69px;}
      .icon01,
      .text01 {background-color: #ff8057;}
      .icon02,
      .text02 {background-color: #f25872;}
      .icon03,
      .text03 {background-color: #28b2a5;}
      .icon04,
      .text04 {background-color: #925fbe;}
    }
  }
  .btn-setup {
    cursor: pointer;
    width: 30px;
    height: 30px;
    margin: 0 auto;
    background-position : center;
    background-repeat: no-repeat;
    background-image: url(#{$img_url}setup_icon_02.png);
  }
  #main01 #myTab {margin-top: 30px;}
}

.inputAddUnit{
  padding-right:30px;
  display: inline-block;
  &+span{
    margin-left: -25px;
  }
}
.unit-desc {
  display:inline-block;
  margin-left:20px;
}
.datepicker{
  .dropdown-menu{
    th, td{
      padding: 7px 10px;
    }
  }
  table{
    thead tr:first-child th:hover, tfoot tr th:hover{
      background:transparent; color:$main_bg_color;
    }
    tr{
      th{
        border-radius:0;
      }
      td{border-top-color: transparent;
        &, span{border-radius:0;}
        &.old, &.new{color:$td_bd_clr;}
        &.day:hover, &.day.focused, span:hover{background:$main_bg_color; color:#fff;}
      }
    }
  }
}

.searchInputCalendar{
  margin-top:$cntn_pd / 2;
  margin-bottom:0;
  .control-label {margin-right:$cntn_pd / 2;}
  .input-group-addon {padding:4px 12px;}
  .input-group.date {float:left;width:46%;}
  .input-group.date:last-child {float:right;}
  .input-group-gubun {float:left;padding:0;width:8%;line-height:$btn_h;text-align:center;}
  .btn {
    float:right;
    width:48%;
    &:first-child {float:left}
    &:only-child {width:100%;}
  }
  @media (max-width:$md_max_w_m) {
    margin-top:0;
    padding-bottom:$cntn_pd_m;
    .col-md-10 {padding-left:0;}
    .row {margin-right:0;margin-left:0;}
    .control-label {margin-top:10px;margin-bottom:5px;width:100%;}
    .input-group.date:first-child {clear:both;}
    .text-right {text-align:left;}
    div[class*="col-md-2"]:last-child {
      clear:both;
      margin-top:$cntn_pd_m * 2;
      padding-top:$cntn_pd_m;
      border-top:1px solid $border_clr;
    }
  }
}

