/* bootstrap-edit s ========================================= */
.form-control {
  height: $btn_h;
  border: 1px solid $input_bd_clr;
  font-size: 1.2rem;
}
.form-control[disabled],
.form-control[readonly],
fieldset[disabled] .form-control {
  color:$input_disable_fc_clr;
  background-color:$input_disable_bg;
}
.row {
  margin-right:-$cntn_pd;
  margin-left:-$cntn_pd;
  @media (max-width:$md_max_w_s){
    margin-right:0;
    margin-left:0;
  }

}
.col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12,
.col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9,.col-md-10, .col-md-11, .col-md-12,
.col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12,
.col-xs-1,  .col-xs-2, .col-xs-3, .col-xs-4, .col-xs-5, .col-xs-6, .col-xs-7, .col-xs-8, .col-xs-9, .col-xs-10, .col-xs-11, .col-xs-12 {
  position:relative;
  min-height:1px;
  padding-right:$cntn_pd;
  padding-left:$cntn_pd;
  @media (max-width:$md_max_w_s){
    padding-right:0;
    padding-left:0;
  }
}
.col-md-pd-0 {
  padding-right:0;
  padding-left:0;
}
div, a, button, form, input, .btn, .btn-xs{
  border-radius:0 !important;
}
select {
  height:$btn_h;
  padding:0 10px;
  border:1px solid $td_bd_clr;
}
.fa-calendar::before {
  content:url('#{$img_url}#{$i_calendar}');
}
.form-horizontal .form-group {margin-right:0;margin-left:0;}
.form-group {
  margin-bottom:20px;
  label {
    padding-top:0;
  }
}
label.inputReq:after {
  content: " *";
  color: red;
  font-weight: bold;
  padding-left:3px;
}
.label {
  display: inline;
  padding: 5px 10px;
  font-size: 12px;
  font-weight: 400;
  background-color: #fff;
  vertical-align: middle;
  border-radius: 0;
  &.label-blank {
    border: 1px solid $main_bg_color_l_1;
    color: $main_bg_color_l_1;
    &[href] {
      &:hover,
      &:focus {
        background-color: $main_bg_color_d_1;
        color: #fff;
      }
    }
  }
  &.display {
    text-align: center;
    min-width: 100px;
    visibility: hidden;
    &:after {
      display: inline;
      margin-left: 5px;
      content: url(img/check_active.png);
    }
    &.active {
      visibility: visible;
    }
  }
}

p {margin:0;}

.form-horizontal .checkbox,
.form-horizontal .checkbox-inline,
.form-horizontal .radio,
.form-horizontal .radio-inline {
  padding-top:0;
}

.chck-list {
  &.with-select {
    margin-top:6px;
    margin-left:10px;
  }
}

span.i_secret {
  display:inline-block;
  width:13px;
  height:15px;
  margin-right:5px;
  background:url('#{$img_url}#{$i_secret}') no-repeat;
  vertical-align:middle;
}
span.i_answered {
  display:inline-block;
  width:13px;
  height:15px;
  margin-right:5px;
  background:url('#{$img_url}#{$i_answered}') no-repeat;
  vertical-align:middle;
}

.bootstrap-datetimepicker-widget {
  z-index:9999;
  th,
  td {
    width:initial;
    min-width:initial;
  }
}

/* icheck */
.icheckbox_square-blue,
.iradio_square-blue {
  background-image:url('#{$img_url}#{$icheck_square_img}');
  background-repeat:no-repeat;
  margin-right:5px;
  margin-left: 10px;
}
.icheckbox_minimal-blue,
.iradio_minimal-blue {
  background-image:url('#{$img_url}#{$icheck_minimal_img}');
  background-repeat:no-repeat;
  margin-right:5px;
}
.checkbox-inline,
.radio-inline {
  padding-left:0;
  margin-right:15px;
  > div{
    margin-left: 10px;
    &:first-child {
      margin-left:0;
    }
  }
}
.breadcrumb {
  > .active {
    color:#000;
  }
}
