/* tree s =================================================== */
.content {
  > .row {
    > .lt-box-pad.box-tree-wrap {
      position: relative;
      .box.box-tree {
        //position: fixed;
        //top: 121px;
        //left: $sidebar_w + $cntn_pd;
        position:absolute;
        top:0;
        left:$cntn_pd;
        padding:0 !important;
        width: 300px;
        transition: left 0.3s ease-in-out;
        @media (max-width:$md_max_w_s) {
          position: relative;
          top: 0;
          left: 0;
          width: auto;
        }
      }
      + .rt-box-pad {
        float:right;
        width:calc(100% - 300px - #{$cntn_pd});
        @media (max-width:$md_max_w_s) {
          width:100%;
        }
      }
    }

  }
}
.sidebar-collapse {
  .content {
    > .row > .lt-box-pad {
      .box.box-tree {
        left: $sidebar_mini_w + $cntn_pd;
      }
    }
  }
}
.box.box-tree {
  /*padding-top:50px !important;*/
  padding-top:10px !important;
  height:calc(100vh - 50px - 50px - 20px - 40px - 20px - 50px);
  overflow:auto;
  .box-header {
    padding-bottom:0 !important;
    .box-title {border:0 !important;}
  }
  .box-body {
    border-top:0;
  }
  .tree-select-wrap {
    margin:0;
    padding:7px 20px 0;
    display:inline-block;
    width:100%;
    list-style:none;
    background:#f7f7f7;
    border-bottom:1px solid $border_clr;
    li {
      clear:both;
      div[class*="col-md-6"] {
        padding:0 0 8px 0;
        &:nth-child(1) {
          padding-right:3px;
        }
        &:nth-child(2) {
          padding-left:3px;
        }
      }
    }
  }
  .treeBtn-wrap {
    float:left;
    margin-bottom:20px;
    padding:7px 20px;
    width:100%;
    background:#f7f7f7;
    border-bottom:1px solid $border_clr;
    .treeBtn {
      float:left;
      display:inline-block;
      padding:10px 9px;
      width:48%;
      border:1px solid $border_clr;
      color:$cntn_fc_h2;
      text-align:center;
      background:#fff;
      &:first-child {
        margin-right:4%;
      }
      i {
        margin-left:5px;
        font-size:20px;
        color:$border_clr;
        vertical-align:middle;
      }
      /*
      @media (max-width:$md_max_w_l) {
        margin-left:0;
        margin-bottom:5px;
        width:100%;
      }
      */
    }
  }
  .tree-btn-wrap {
    margin-bottom:20px;
    width:100%;
    padding-bottom:10px;
    .btn {
      width:100%;
    }
  }
  .tree-cont {
    clear:both;
    /*margin-top:20px;*/
    padding-top:20px;
    padding-right:20px;
    padding-left:20px;
    //border-top:1px solid $border_clr;
    > div {
      .dateBtn01 {
        box-sizing:border-box;
        border:1px solid $border_clr;
      }
    }
    ul.easyui-tree {
      /*  tree-line, tree-join  */
      li > div {
        /*margin: 7px 0;*/
        margin: 0 0;
        .tree-collapsed {
          //background:url('#{$img_url_tree}tree_folder_default_mark.png') no-repeat 50%;
          background:url('#{$img_url_tree}tree_folder_select_close_mark_v2.png') no-repeat 50%;
        }
        .tree-expanded {
          //background:url('#{$img_url_tree}tree_folder_open_mark.png') no-repeat 50%;
          background:url('#{$img_url_tree}tree_folder_select_open_mark_v2.png') no-repeat 50%;
        }
        .tree-folder {
          /*background:url('#{$img_url_tree}tree_folder_default.png') no-repeat 50%;*/
          //background:url('#{$img_url_tree}tree_folder_select_close.png') no-repeat 50%;
          background:url('#{$img_url_tree}tree_folder_select_close_v2.png') no-repeat 50%;
        }
        .tree-folder-open {
          /*background:url('#{$img_url_tree}tree_folder_open.png') no-repeat 50%;*/
          //background:url('#{$img_url_tree}tree_folder_select_open.png') no-repeat 50%;
          background:url('#{$img_url_tree}tree_folder_select_open_v2.png') no-repeat 50%;
        }
        .tree-file {
          background:url('#{$img_url_tree}tree_folder_default_dot.png') no-repeat 100% 50%;
          background-size:10%;
        }
        &.tree-node {
          color:#1d1d1d;
        }
        &.tree-node-selected {
          background:none;
          font-weight:$md;
          color:$point_clr2;
          .tree-collapsed {
            //background:url('#{$img_url_tree}tree_folder_select_close_mark.png') no-repeat 50%;
            background:url('#{$img_url_tree}tree_folder_select_close_mark_v2.png') no-repeat 50%;
          }
          .tree-expanded {
            //background:url('#{$img_url_tree}tree_folder_select_open_mark.png') no-repeat 50%;
            background:url('#{$img_url_tree}tree_folder_select_open_mark_v2.png') no-repeat 50%;
          }
          .tree-folder {
            //background:url('#{$img_url_tree}tree_folder_select_close.png') no-repeat 50%;
            background:url('#{$img_url_tree}tree_folder_select_close_v2.png') no-repeat 50%;
          }
          .tree-folder-open {
            //background:url('#{$img_url_tree}tree_folder_select_open.png') no-repeat 50%;
            background:url('#{$img_url_tree}tree_folder_select_open_v2.png') no-repeat 50%;
          }
          .tree-file {
            background:url('#{$img_url_tree}tree_folder_select_dot.png') no-repeat 100% 50%;
            background-size:10%;
          }
        }
        .tree-title2 {
          padding-left:5px;
        }
      }
    }
  }
  .nav-stacked {
    li {
      border-bottom:0;
      a {
        padding:10px 5px;
        .fa-circle {
          display: none;
          font-size:5px;
          color:$cntn_fc_c1;
        }
      }
      &:hover,
      &.active {
        a {
          span,
          .fa-circle {
            border-left-color:transparent;
            color:$point_clr2;
            background-color:#fff;
          }
        }
      }
    }
  }
}
.box.box-tree-cont {
  position:relative;
  min-height:calc(100vh - 50px - 50px - 20px - 40px - 20px - 50px);
  .refresh-btn-wrap {
    position:absolute;
    top:$cntn_pd / 2;
    right:$cntn_pd;
    @media (max-width:$md_max_w_s) {
      position:relative;
      top:0;
      right:0;
    }
  }
}
