/* pages s =================================================== */
.tit-icon {
  display:inline-block;
  width:30px;
  height:30px;
  //vertical-align:bottom;
  @include f_tit_icon_make(); //tit icon draw
}


.content.goodsInfo-wrap {
  .box.box-search {
    .col-md-2.no-border {padding-top:0 !important;}
  }
}

.content.insertGoodsInfo-wrap {
  .option-btnbox {float:none;margin-bottom:20px;}
  .tab-content {
    @media (min-width:$md_min_w_l){
      .tab-pane:first-child .box-title+div:first-of-type{
        .row > div{
          padding-right:0;
          padding-left:10px;
          &:first-of-type{
            padding-left:20px;
          }
          button{
            margin-left:0;
            width: auto;
          }
        }
      }
    }
    margin-top:0;
    .box {
      .box-title {
        padding-top:20px;
        padding-bottom:10px;
        padding-left:$cntn_pd;
        border-bottom:0;
        @media (max-width:$md_max_w_s){
          /*margin-left:0;*/
          padding-left:0;
        }
      }
      .form-group-label {float:left;line-height:$btn_h;}
    }
    .bar-list{
      list-style: none;
      margin-bottom: 20px;
      li{
        display: inline-block;
        margin: 5px 0 0 5px;
        padding-left: 5px;
        border-left: 1px solid $border_clr;
        &:first-child{
          margin-left: 0;
          padding-left: 0;
          border-left: 0;
        }
      }
    }
    .col-md-4.row {
      .checkbox-inline,
      .radio-inline {margin-right:0;}
    }
    .form-group {
      margin-bottom:$cntn_pd / 2;
    }
  }

  .input-group-gubun-2 {
    float:left;
    padding:0;
    line-height:$btn_h;
    text-align:center;
  }

  .input-group-addon-2{
    padding: 5px 12px;
  }
}

.content.codeInfo-wrap {
  .tree-btn-wrap .treeBtn {width:47.5%;margin-left:5px;}
}

.content.orderList-wrap {
  .nav-tabs-custom {
    > .nav-tabs {
      > li {
        // width:12.5%;
        width:8.5%;
        @media (max-width:$md_max_w_l){
          width:16%;
        }
        a {
          padding-left:0;
          padding-right:0;
        }
      }
    }
  }

  #tab-5{
    .form-control[disabled], .form-control[readonly], fieldset[disabled] .form-control{
      margin-bottom: 10px;
    }
  }
}

.content.orderDetail-wrap {
  @media (max-width:$md_max_w_s){
    padding:0;
  }
}

.content.bannerList-wrap {
  .box {
    &.box-tree {
      .fa.fa-circle{
        display:none;
        color:$cntn_fc_c1;
        font-size:8px;
      }
      .nav-stacked li a:before{
        content:'\2022';
        float:left;
        margin-top:-9px;
        margin-right:0;
        font-size:24px;
        color:$cntn_fc_c1;
      }
      .nav-stacked > li.active > a, .nav-stacked > li.active > a:hover, .nav-stacked > li.active > a:focus {
        &,
        >.fa-circle {
          border-left-color: transparent;
          color: $point_clr2;
          // background-color: #fff;
        }
      }
    }
  }
  .nav-tabs > li {
    @media (max-width:$md_max_w_m) {
      width:50%;
    }
  }
  .tab-content {
    overflow-y: auto;
    .table{
      *{
        text-align: center;
      }
      a > i{
        color: $cntn_fc_c1;
      }
    }
  }
  .table tr:last-child td {
    background-color:$cntn_bg_clr;
    border-bottom:$th_bd_clr;
  }
  .modal-dialog {
    width:800px;
    @media (max-width:$md_max_w_s){
      width:90%;
    }
    .shopInfoWrap {position:relative;}
    .releaseYnWrap {
      position:absolute;
      top:-10px;
      right:$cntn_pd;
      @media (max-width:$md_max_w_s){
        right:0;
      }
    }
    .input-group-addon {padding:4px 12px;}
    .input-group.date {
      float:left;
      width:45%;
    }
    .input-group-gubun {float:left;padding:0;line-height:$btn_h;text-align:center;}
  }
}

.content.mall-wrap {
  .box {clear:both;}
  .option-txtbox  {font-size:15px; margin-bottom:20px;}
  @media (max-width:$md_max_w_s){
    .box .nav-tabs, .nav-tabs-custom > .tab-content {
      /*margin-right:-$cntn_bx_pd - $cntn_pd_m;
      margin-left:-$cntn_bx_pd - $cntn_pd_m;*/
      margin-right:-$cntn_pd;
      margin-left:-$cntn_pd;
    }
  }
}

.content.boardEdit-wrap {
  .box-body {
    &:last-child {margin-top:$cntn_pd;}
    .col-md-3 {
      > label:first-child {
        display:block;
        @media (max-width:$md_max_w_m) {
          display:inline-block;
          min-width:100px;
        }
      }
      label.radio-inline {
        padding-left:0;
        padding-right:20px;
        @media (max-width:$md_max_w_m) {
          padding-left:20px;
          padding-right:0;
        }
      }
    }
  }
}

.content.writingList-wrap {
  .box.box-search {
    .m-hide {
      clear:both;
      @media (max-width:$md_max_w_m) {
        display:none;
      }
      & + div[class*=col-] {
        padding-right:0;
      }
    }
  }
  a.btn,
  .btn {
    @media (max-width:$md_max_w_m) {
      padding:0 15px;
    }
  }
}

.box.box-tree .nav-stacked li a{
  background: transparent;
  padding-left: 0;
}

.content.writingEdit-wrap {
  .write-table {
    &.table {margin-top:0;}
    tbody {
      border-top:0 !important;
      tr {
        .form-group{margin-bottom:0;}
        th {
          background-color:$th_bg_clr;
          white-space:nowrap;
          &[rowspan]+td {padding-bottom:0;}
        }
        td:last-child, td:only-child{padding-right: 20px;}
        td, th {
          height:50px;
          padding-right:20px;
          padding-left:20px;
          *:first-child {
            &.btn {padding-left:29px;}
            &[type='text'] {padding-left:12px;}
          }
        }
        @media (max-width:$md_max_w_s) {
          td, th{height: 100px;}
        }
      }
    }
  }
}

.content.companyInfo-wrap{
  /*  .inputAddUnit{
      padding-right:30px;
      display: inline-block;
      &+span{margin-left: -25px;}
    }*/
  .input-group-md{
    display: block;
    > input {
      width: 32%;
      margin-right: 5px;
    }
  }
  .input-group-addon{padding-top:5px; padding-bottom:5px;}
  .input-group.date:first-child{float:left; width:47%;}
  .input-group-gubun{float: left; margin: 0 10px; line-height:30px;}


  @media (max-width:$md_max_w_m) {
    input[readonly]{margin-top: -15px; margin-bottom: 10px;}
  }
  /* @media (max-width:$md_max_w_s) {
      label.radio-inline{margin-left: 20px;}
    }*/
}

.content.deliveryTemplInfo-wrap{
  input[readonly]{margin-top: -15px; margin-bottom: 10px;}
  .address1{margin-top:5px !important; margin-bottom:5px !important;}
  .bundDelvDetail{display:inline; color:#5e86c9; font-weight:bold;}
  .bundDelvSearch{float:right; margin-top:-5px;}
  .delvCondTp{width:auto; display:inline-block;
    &+div{display:inline-block;}
  }
  .delvCondFree{width:auto; display:inline-block;}

  .modal-content{
    .delvCondTp{width:auto; display:inline-block;}
    .input-group-btn {
      border-top:1px solid $border_clr;
      border-right:1px solid $border_clr;
      border-bottom:1px solid $border_clr;
      > .btn {
        width: 100%;
        height:$btn_h - 2px;
        margin-left:0;
        margin-bottom:0;
        padding:0;
        &.input-group-addon {padding:0 10px;line-height:1 !important; background:$btn_bg_clr1; }
        .idrm-icon-search::before {
          content:url(../images/common/icon_lnb_search.png);
        }
      }
    }
    .selectBundleYn{margin:0;margin-right:-10px;}
  }
  td label{margin:0;margin-right:-10px;}
  @media (max-width:$md_max_w_s) {
    .bundDelvSearch{float:none; width:100%;}

  }
}

.content.deliveryBundleList-wrap{
  .box.box-search .form-group .btn{
    &.btn-primary{width:48%; float: left;}
    &.btn-danger{width:48%; float: right;}
  }
  td label{margin:0;margin-right:-10px;}
}

.content.promotionInfo-wrap{
  .option-btnbox{
    margin-bottom: 20px;
    >li{
      float: left;
    }
  }
  .input-group-addon{
    padding: 5px 12px;
  }
  .input-group-gubun {
    float:left;
    padding:0;
    line-height:$btn_h;
    text-align:center;
  }
  .inputAddUnit + span{
    height: 30px;
    line-height: 30px;
  }

  .modal-content{
    .input-group-btn {
      border-top:1px solid $border_clr;
      border-right:1px solid $border_clr;
      border-bottom:1px solid $border_clr;
      > .btn {
        width: 100%;
        height:$btn_h - 2px;
        margin-left:0;
        margin-bottom:0;
        padding:0;
        &.input-group-addon {padding:0 10px;line-height:1 !important; background:$btn_bg_clr1; }
        .idrm-icon-search::before {
          content:url('#{$img_url}#{$i_search}');
        }
      }
    }
    .btn{margin-left: 0;}
    .collapsed-btn{
      width: 48%;
    }
  }
}

.price-per-discount {
  input {
    width:40px !important;
    height:$btn_h2;
    margin-left:10px;
  }
}

.no-page-wrap {
  display:flex;
  justify-content:center;
  align-items:center;
  height:calc(100vh - 50px - 76px - 51px);
}

.role-wrap {
  div[class*="col-md-"]:first-child {
    .box-body {
      margin-top:$btn_h3;
    }
  }
  .box-body {
    table {
      margin-top: 10px;
      th,
      td {
        width:auto !important;
      }
    }
  }
  .role-edit-btns {
    padding-top:$btn_h3 + 45px;
    .btn {
      display:block;
      margin-right:auto;
      margin-left:auto;
      margin-bottom:10px;
      padding:3px 20px;
    }
  }
  .btn-form-wrap {
    text-align:right;
  }
}
