/* tab s ==================================================== */
.nav-tabs-custom {
  box-shadow:none;
  /*margin-top:20px;*/
  > .nav-tabs {
    // border-bottom:2px solid $point_clr2;
    border-bottom:1px solid $border_clr;
    //@include setFullBox();
    > li {
      margin-right:0;
      border:0;
      a {
        // padding:15px 30px;
        // padding:6px 30px 7px;
        padding:6px 15px 7px;
        font-weight:$r;
        font-size:1.4rem;
        text-align:center;
        color:$cntn_fc_h2;
      }
      &:hover > a {
        color:$point_clr2;
      }
      &.active {
        // border:2px solid $point_clr2;
        // border-bottom:0;
        border-bottom:2px solid $point_clr2;
        a,
        &:hover a {
          color:$point_clr2;
          border-bottom:0;
        }
      }
    }
  }
  > .tab-content {
    padding:20px 20px 0 20px;
  }
  @media (max-width:$md_max_w_m) {
    > .nav-tabs {
      //margin-right:-$cntn_pd_m;
      //margin-left:-$cntn_pd_m;
      //margin-bottom:10px;
      border-bottom:1px solid $border_clr;
      > li {
        width:25% !important;
        border-top:1px solid $border_clr;
        border-right:1px solid $border_clr;
        border-bottom:1px solid $border_clr;
        &:nth-child(4n+4) {
          border-right:1px solid #fff;
        }
        &:nth-child(n+5) {
          border-top:1px solid #fff;
        }
        &:nth-child(n+5),
        &:last-child {
          border-bottom:0;
        }
        &:first-child {
          border-left:1px solid $border_clr;
        }
        &.active {
          border:1px solid $point_clr2;
          border-bottom:2px solid $point_clr2;
          &:hover a {
            //border-bottom:1px solid $border_clr;
          }
          &:nth-child(4n+4) {
            border-right:1px solid $point_clr2;
          }
          &:nth-child(n+5) {
            border-top:1px solid $point_clr2;
          }
          a {
            border-bottom:0 !important;
          }
        }
        &:nth-last-child(1),
        &:nth-last-child(2),
        &:nth-last-child(3),
        &:nth-last-child(4) {
          border-bottom:1px solid #fff;
          a {
            border-bottom:1px solid $border_clr;
          }
        }
        a {
          //height:55px;
          //line-height:55px;
          text-align:center;
          border-bottom:1px solid #fff;
        }
      }
    }
  }
  @media (max-width:$md_max_w_s) {
    border-bottom:0;
    > .nav-tabs {
      > li {
        width:50% !important;
        &:nth-child(2n+2) {
          border-right:1px solid #fff;
          border-bottom:1px solid $border_clr;
        }
        &:nth-child(n+3) {
          border-top:1px solid #fff;
          border-bottom:1px solid $border_clr;
        }
        &.active {
          border:1px solid $point_clr2;
          border-bottom:1px solid $border_clr;
          &:nth-child(2n+2) {
            border-right:1px solid $point_clr2;
          }
          &:nth-child(n+3) {
            border-top:1px solid $point_clr2;
          }
        }
        &:nth-last-child(1),
        &:nth-last-child(2) {
          border-bottom:1px solid #fff;
          a {
            border-bottom:1px solid $border_clr;
          }
        }
      }
    }
  }
}

.nav-subtabs-custom {
  clear:both;
  .nav-tabs {
    margin-bottom:$cntn_pd;
    > li {
      a {
        font-weight:600;
        font-size:14px;
        text-align:center;
        color:$cntn_fc_h2;
      }
      &:hover > a {
        color:$point_clr2;
      }
      &.active {
        a,
        &:hover a {
          color:$point_clr2;
        }
      }
    }
  }
  .tab-content {
    .option-btnbox {
      margin-bottom:($cntn_pd / 2);
    }
  }
}

#tab-contents-info {
  .col-md-6 {
    .table-responsive {
      height:200px;
      overflow:auto;
    }
  }
  .col-md-l {
    .row {
      padding-right:10px;
    }
  }
  .col-md-r {
    .row {
      padding-left: 10px;
    }
  }
}

.tab-con-box {
  .tab-con-title {
    padding:($cntn_pd - 10px) $cntn_pd;
    font-size:18px;
    color:#fff;
    background:#303d4d;
  }
  .subpage-title {
    margin-top:$cntn_pd !important;
    font-weight:normal !important;
  }
  label {
    font-weight:normal;
  }
}
