/* base s =================================================== */
* {
	@include bxzb;
	/*outline: none;*/
	/*font-weight: normal;*/
	font-style: normal;
}
html {
	font-size:62.5%; //em, rem
}
html,body {
	width:100%;
	height:100%;
	&.back {
		background-color: #f5f5f5;
	}
}
body {
	-webkit-font-smoothing: antialiased;
	font: {
	      family:$default_font, $second_font, tahoma, dotum, Apple-Gothic, sans-serif;
	      size:1.2em;
	      weight: normal;
	   }
	color:$gray_fc;
	letter-spacing:-0.02em;
	-webkit-text-size-adjust:none;
	&.hidden {
		overflow: hidden;
	}
}
a {
	text-decoration:none;
	color: $btn_bg_clr1;
	&:hover,
	&:focus {
		color:#333;
	}
}
address,
em {
	font-style:normal;
}
hr {
	//display:none;
}

/* table */
table {
	width:100%;
	border-collapse: separate;
}
th,
td {
	word-break:break-all;
	background-clip: padding-box;
	&.posr {
		position: relative;
	}
}
caption {
	overflow:hidden; 
	font-size:medium; 
	line-height:normal; 
	width:100%; 
	height:0; 
	word-break:break-all; 
	opacity:0; 
	filter:alpha(opacity=0); 
	-ms-filter:'progid:DXImageTransform.Microsoft.Alpha(opacity=0)';
}
fieldset {
	border:none;
}

/*  form */
input {
	-webkit-border-radius: 0;
}
input[type=radio],
input[type=checkbox] {
	@include css3-prefix(appearance, none); padding:0; border:0; background:none;
}
input[type=text],
input[type=password],
input[type=number] {
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	height:$btn_h;
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
button {
	width:auto;
	border:0;
	padding: 0;
	background:none;
	font-weight:normal;
}
textarea {
	resize:none;
	border:1px solid #bfbfbf;
	-webkit-border-radius: 0;
}
a,
img,
input,
button,
select,
textarea {
	vertical-align:middle;
}
a, button {
	&:focus, &:active, &:hover {
		text-decoration: none;
		outline:none;
	}
}
ul, ol, dl {
	margin: 0;
	padding: 0;
}
li, dd {
	margin: 0;
	padding: 0;
	list-style: none;
}
p {
	margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	font-weight: normal;
	//line-height: 100%;
}
